 
  .wrapper {
    background: #42A8C0;
    max-width: 1000px;
    margin: 0 auto;
    position: relative;
    -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
  }
  .sidebar-wrapper {
    background: -webkit-gradient(linear,left bottom,right top,color-stop(0%,rgba(0, 255, 169, 0.4)),color-stop(100%,rgb(2, 174, 191))) left top no-repeat;
    height: 100%;
    min-height: 800px;
    color: #545E6C;
  }
  .sidebar-wrapper a {
    color: #fff;
    text-decoration: none;
  }
  .sidebar-wrapper .profile-container {
    padding: 30px;
    background: rgba(0, 0, 0, 0.2);
    text-align: center;
    color: #fff;
  }
  
  .sidebar-wrapper .name {
    font-size: 32px;
    font-weight: 900;
    margin-top: 0;
    margin-bottom: 10px;
  }
  
  .sidebar-wrapper .tagline {
    color: rgba(255, 255, 255, 0.6);
    font-size: 16px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 0;
  }
  .sidebar-wrapper .profile {
    margin-bottom: 15px;
  }
  .profile-img {
    max-width: 100px;
    margin-bottom: 15px;
    border: 3px solid #fff;
    border-radius: 100%;
  }
  .sidebar-wrapper .contact-list .fa {
    margin-right: 5px;
    font-size: 18px;
    vertical-align: middle;
  }
  .sidebar-wrapper .contact-list li {
    margin-bottom: 15px;
  }
  .sidebar-wrapper .contact-list li:last-child {
    margin-bottom: 0;
  }
  .sidebar-wrapper .contact-list .email .fa {
    font-size: 14px;
  }
  .sidebar-wrapper .container-block {
    padding: 30px;
  }
  .sidebar-wrapper .container-block-title {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 15px;
  }
  .sidebar-wrapper .degree {
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 5px;
  }
  .sidebar-wrapper .education-container .item {
    margin-bottom: 15px;
  }
  .sidebar-wrapper .education-container .item:last-child {
    margin-bottom: 0;
  }
  .sidebar-wrapper .education-container .meta {
    color: rgba(255, 255, 255, 0.6);
    font-weight: 500;
    margin-bottom: 0px;
    margin-top: 0;
    font-size: 13px;
  }
  .sidebar-wrapper .education-container .time {
    color: rgba(255, 255, 255, 0.6);
    font-weight: 500;
    margin-bottom: 0px;
  }
  .sidebar-wrapper .languages-container .lang-desc {
    color: rgba(255, 255, 255, 0.6);
  }
  .sidebar-wrapper .languages-list {
    margin-bottom: 0;
  }
  .sidebar-wrapper .languages-list li {
    margin-bottom: 10px;
  }
  .sidebar-wrapper .languages-list li:last-child {
    margin-bottom: 0;
  }
  .sidebar-wrapper .interests-list {
    margin-bottom: 0;
  }
  .sidebar-wrapper .interests-list li {
    margin-bottom: 10px;
  }
  .sidebar-wrapper .interests-list li:last-child {
    margin-bottom: 0;
  }
  .main-wrapper {
    background: #fff;
    padding: 0 60px;
  }
  .main-wrapper .section-title {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
    color: rgba(2, 174, 191, 0.88);
    position: relative;
    margin-top: 0;
    margin-bottom: 20px;
  }
  .main-wrapper .section-title .fa {
    width: 30px;
    height: 30px;
    margin-right: 8px;
    display: inline-block;
    color: #fff;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    background: rgba(2, 174, 191, 0.88);
    text-align: center;
    padding-top: 8px;
    font-size: 16px;
    position: relative;
    top: -2px;
  }
  .main-wrapper .section {
    margin-bottom: 60px;
  }
  .main-wrapper .experiences-section .item,
  .main-wrapper .educations-section .item {
    margin-bottom: 30px;
  }
  .main-wrapper .upper-row {
    position: relative;
    overflow: hidden;
    margin-bottom: 2px;
  }
  .main-wrapper .job-title,
  .main-wrapper .degree {
    color: #3F4650;
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 500;
  }
  .main-wrapper .time {
    position: absolute;
    right: 0;
    top: 0;
    color: #97AAC3;
  }
  .main-wrapper .company,
  .main-wrapper .university {
    margin-bottom: 10px;
    color: #97AAC3;
  }
  .main-wrapper .project-title {
    font-size: 16px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 5px;
  }
  .main-wrapper .projects-section .intro {
    margin-bottom: 30px;
  }
  .main-wrapper .projects-section .item {
    margin-bottom: 15px;
  }
  .main-wrapper .projects-section .item .project-tags{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 5px;
  }
  .main-wrapper .projects-section .item .projects-tags-item{
    border: 1px solid #42A8C0;
    padding: 5px;
    border-radius: 5px;
    margin: 0 5px 5px 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 11px;
    line-height: 1;
    transition: all .2s ease-in-out;
    cursor: default;
  }
  .main-wrapper .projects-section .item .projects-tags-item:hover{
    background: #42A8C0;
    border-color: white;
    color: white;
  }
  .main-wrapper .publication-authors {
    font-style: italic;
    margin-bottom: 1px;
  }
  .main-wrapper .publications-section .intro {
    margin-bottom: 30px;
  }
  .main-wrapper .publications-section .item {
    margin-bottom: 15px;
  }
  .skillset .item {
    margin-bottom: 15px;
    overflow: hidden;
  }
  .skillset .level-title {
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 12px;
  }
  .skillset .level-bar {
    height: 20px;
    background: #f5f5f5;
  }
  .skillset .level-bar-inner {
    height: 20px;
    background: rgba(2, 174, 191, 0.88);
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    font-size: 10px;
  }
  .footer {
    padding-top: 30px;
  }
  .footer .copyright {
    line-height: 1.6;
    color: #545E6C;
    font-size: 13px;
  }
  .footer .fa-heart {
    color: #fb866a;
  }
  /* Extra small devices (phones, less than 768px) */
  @media (max-width: 767px) {
    body {
      padding: 0;
    }
    .sidebar-wrapper {
      position: static;
      width: inherit;
    }
    .main-wrapper {
      padding: 30px;
    }
    .main-wrapper .time {
      position: static;
      display: block;
      margin-top: 5px;
    }
    .main-wrapper .upper-row {
      margin-bottom: 0;
    }
   
  }
  
  @media (min-width: 767px) {
    .sidebar-wrapper {
      background: -webkit-gradient(linear,left bottom,right top,color-stop(0%,rgba(0, 255, 169, 0.4)),color-stop(100%,rgb(2, 174, 191))),url(../images//rightbg.jpg) left top no-repeat;
    }
  }
  /* Small devices (tablets, 768px and up) */
  /* Medium devices (desktops, 992px and up) */
  @media (min-width: 992px) {
  
    .skillset .level-title {
      display: inline-block;
      float: left;
      width: 30%;
      margin-bottom: 0;
    }
    .skillset .level-bar {
      display: inline-block;
      width: 70%;
      float: left;
      position: relative;
      top: 1px;
    }
  }
  .summary .citation{
    border-left: 3px solid #ccc;
    padding-left: 10px;
  }

  .list-unstyled {
    padding-left: 0;
    list-style: none;
    text-decoration: none;
  }   
  /* Large devices (large desktops, 1200px and up) */
  /* Ex-Large devices (large desktops, 1200px and up) */
  /* styles.css */
  
  @media print{
    .profile-img{
      width: 100px;
    }
  
    .projects-section , .skills-section, .footer{
      display: none;
    }
  
    a[href]:after {
      content: none !important;
    }
  }
  
